@import url('../../index.css');

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading_spinner {
  width: 50px;
  height: 50px;
  border: 10px solid var(--color-purple-gradient-some); /* #f3f3f3;  Light grey */
  border-top: 10px solid var(--color-violet); /* #383636; Blue */
  border-radius: 50%;
  animation: spinner 1.5s linear infinite;
}

.spinner_container {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 350px;
}
